.app-header {
  background-color: #191c21;
  padding: 16px 16px;
  border-bottom: 1px solid #292d33;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100vw;
  z-index: 100;
}

.app-header h1 {
  margin: 0;
  font-size: 18px;
  font-weight: 500;
  color: white;
  display: flex;
  align-items: center;
  gap: 8px;
  font-family: "Lato", sans-serif;
}

.version-number {
  font-size: 12px;
  color: #4f5259;
  font-weight: 400;
  margin-top: 7px;
  font-family: "Lato", sans-serif;
}

.header-actions {
  display: flex;
  gap: 8px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.section-header h3 {
  margin: 0;
}
